@import url(https://fonts.googleapis.com/css?family=Raleway&display=swap);
body {
  font-family: 'Raleway', sans-serif; }

#root {
  padding: 10px; }

.game {
  display: flex;
  flex-direction: column;
  align-items: center; }

.game-info p {
  margin-top: 0px; }

ol, ul {
  padding-left: 12px; }
  ol li, ul li {
    padding: 3px 0; }

.board-row {
  display: flex;
  flex-direction: row;
  align-items: flex-start; }

.board-row:after {
  clear: both;
  content: "";
  display: table; }

.status {
  margin-bottom: 10px; }

.square {
  background: #fff;
  border: 1px solid #999;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  height: 34px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 34px; }

.square:focus {
  outline: none; }

.kbd-navigation .square:focus {
  background: #ddd; }

